
.calendar {
  display: grid;
  grid-template-rows: 1fr;
  grid-template-columns: auto;
  grid-auto-flow: column;
  grid-auto-columns: 1fr;
  background:
    repeating-linear-gradient(166deg, transparent, transparent, #e7eefc, transparent 4px),
    repeating-linear-gradient(55deg, transparent, #e8f0fe, transparent 4px);
  padding: 2px;
}

.hourBand {
  grid-row: 1;
}

.hourBand.hourEven {
  background: #fafcff;
  border: solid 1px #e9f0fc;
}

.hourBar {
  grid-row: 1;
  grid-column: 1;
  text-align: right;
  font-size: 90%;
  color: #67758e;
}

.hourBar .hour {
  padding-right: 0.2em;
  padding-left: 0.1em;
  background:
    repeating-linear-gradient(166deg, transparent, transparent, #d2e0ff, transparent 4px),
    repeating-linear-gradient(55deg, transparent, #d8e5fe, transparent 4px);
}

.hourBar .hourEven {
  background: #cfe0ff;
  border-bottom: solid 1px #bed5fe;
  border-top: solid 1px #c0d7ff;
}

.hourBar .hourOdd {
  color: #67758e;
}

.eventContainer {
  pointer-events: none;
}

.event {
  position: relative;
  border: solid 1px #cdd2dc;
  border-right-color: #a5b7d7;
  border-bottom-color: #a5bce5;
  border-radius: 3px;
  overflow-y: hidden;
  background: linear-gradient(to right bottom, #f3f3f3, white);
  margin: 2px;
  pointer-events: all;
  transition: all 300ms;
  padding: 0.25em;
  box-shadow: 1px 1px 4px rgb(0 0 0 / 22%);
}

.event:hover {
  border: solid 1px cornflowerblue;
  overflow-y: initial;
  height: auto !important;
  transform: scale(1.04);
  z-index: 10;
  box-shadow: 2px 2px 10px #002465a1;
}

.event .title {
  font-size: 75%;
}

.event .duration {
  font-size: 70%;
  position: absolute;
  bottom: 0.25em; right: 0.25em;
  text-align: right;
  opacity: 0.6;
}